<script>
export default {
  name: 'Home',
  data: function() {
    return {
      welcome: true,
      randomIndex: 0,
      personIndex: 0,
      currentItem: '',
      itemPhase: '',
      timeCounter: 59,
      timesOut: false,
      timerActive: false,
      gameFinished: false,
      score: 0,
      recipes: [
        {
          name: 'Hamburger',
          icon: 'üçî',
          items: [
            'ü•ñ','ü•¨','ü•©','üßÄ'
          ],
          price: 10
        },
        {
          name: 'Soup',
          icon: 'üçú',
          items: [
            'üßÖ','üçÖ','ü•î','ü•ï'
          ],
          price: 3
        }
      ],
      plate: [],
      cutProgressStyleObject: {
        width: '0px'
      },
      cookProgressStyleObject: {
        width: '0px'
      },
      personStyleObject: {
        'background-position': '-160px'
      },
      cutProgress: 0,
      cutCompleted: false,
      cookProgress: 0,
      cookCompleted: false,
      processInterval: null,
      timerInterval: null
    }
  },
  methods: {
    selectItem: function(item) {
      if (this.recipes[this.randomIndex].items.indexOf(item) === -1 || this.plate.indexOf(item) !== -1) {
        // wrong item
      } else {
        this.currentItem = item;
        this.itemPhase = 'cut';
      }
    },
    process: function (type) {
      if (type === this.itemPhase) {
        var that = this;
        this.processInterval = setInterval(function() {
          if (that[type + 'Progress'] < 3) that[type + 'Progress']++;
          else {
            that[type + 'Completed'] = true;
            that[type + 'Progress']++;
            that[type + 'Progress'] = 0;
            if (type === 'cut') {
              that.itemPhase = 'cook';
            }
            else if (type === 'cook') {
              if (that.plate.indexOf(that.currentItem) === -1) {
                that.plate.push(that.currentItem);
                that.itemPhase = '';
              }
            }
          }
        }, 500);
      }
    },
    cancelProcess: function() {
      clearInterval(this.processInterval);
      this.cookProgress = 0;
      this.cookCompleted = false;
      this.cutProgress = 0;
      this.cutCompleted = false;
    },
    startTimer: function(restart) {
      var that = this;
      this.timerActive = true;
      this.gameFinished = false;
      if (restart) {
        this.timeCounter = 59;
        this.timesOut = false;
      }
      this.timerInterval = setInterval(function() {
        that.timeCounter--;
        if (that.timeCounter === 0) { 
          that.timesOut = true;
          that.timerActive = false;
          clearInterval(that.timerInterval);
        }
      }, 1000);
    },
    newGame: function() {
      this.randomIndex = Math.floor(Math.random() * 2);
      this.personIndex = Math.floor(Math.random() * 58);
      this.startTimer(1);
      this.score = 0;
      this.timeCounter = 59;
      this.currentItem = this.recipes[this.randomIndex];
      this.plate = [];
      this.itemPhase = '';
    },
    startGame: function() {
      this.welcome = false;
      this.newGame();
    }
  },
  watch: {
    personIndex: function(newVal) {
      this.personStyleObject = {
        'background-position': '-' + newVal * 80 + 'px'
      };
    },
    timesOut: function(newVal) {
      if (newVal) {
        this.gameFinished = true;
      }
    },
    cutProgress: function(newVal) {
      this.cutProgressStyleObject = {
        width: 'calc(calc(calc(500px / 3) / 3) * ' + newVal + ')'
      };
    },
    cookProgress: function(newVal) {
      this.cookProgressStyleObject = {
        width: 'calc(calc(calc(500px / 3) / 3) * ' + newVal + ')'
      };
    },
    plate: function(newVal) {
      var that = this;
      if (newVal.length === 4) {
        this.itemPhase = 'ready';
        setTimeout(function() {
          that.itemPhase = '';
          that.score += that.recipes[that.randomIndex].price;
          that.timeCounter += 20;
          that.randomIndex = Math.floor(Math.random() * 2);
          that.personIndex = Math.floor(Math.random() * 58);
          that.currentItem = that.recipes[that.randomIndex];
          that.plate = [];
        }, 1500);
      }
    }
  }
}
</script>

